* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  max-width: 100%;
}

body {
  font-family: "Philosopher", sans-serif;
  overflow-x: hidden;
  max-width: 100%;
}

.container {
  width: 100vw;
  height: 100vh;
  background-color: #12131d;
  color: #ffffff;
}

.download-CV {
  color: #000000;
  text-decoration: none;
  padding: 10px 20px;
  background-color: aqua;
  border: none;
  border-radius: 20px;
  margin-top: 15px;
  cursor: pointer;
  box-shadow: 1px 3px 11px -5px rgba(102, 231, 200, 0.79);
}

.download-CV:hover {
  box-shadow: none;
  background-color: #e7f7fd;
  transition: ease-in 0.4s;
  filter: drop-shadow(0 0 7px #d4f5ff);
  /*Sombrear la img*/
}

.download-CV:active {
  transform: scale(90%);
}

/*Barra o scroll*/
::-webkit-scrollbar {
  width: 15px;
  background: #12131d;
}

::-webkit-scrollbar-thumb {
  background: #00ffd5;
  border-radius: 50px;
}

.Page-Menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #12131d;
  transition: 0.5s;
}

.Page-Menu.active {
  top: -100%;
}

.Page-Menu .Page-Menu-banner {
  /*Contiene la img*/
  position: relative;
  width: 600px;
  height: 100%;
}

.Page-Menu .Page-Menu-banner img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.Page-Menu .Page-Menu-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

.Page-Menu .Page-Menu-nav ul {
  position: relative;
}

.Page-Menu .Page-Menu-nav ul li {
  position: relative;
  list-style: none;
  padding: 0 20px;
  padding: 5px 0;
  /*originalmente aqui es margin*/
  overflow: hidden;
  display: table;
}

.Page-Menu .Page-Menu-nav ul li:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #00ffd5;
  transition: transform 0.5s ease-in-out;
  transform: scaleY(0);
  transform-origin: bottom;
}

.Page-Menu .Page-Menu-nav ul li:hover:before {
  transition: transform 0.5s ease-in-out;
  transform: scaleY(1);
  transform-origin: top;
}

.Page-Menu .Page-Menu-nav ul li a {
  position: relative;
  color: #ffffff;
  text-decoration: none;
  font-size: 4em;
  font-weight: 700;
  line-height: 1em;
  display: inline-block;
  text-transform: uppercase;
  transition: 0.5s ease-in-out;
}

.Page-Menu .Page-Menu-nav ul li a::before {
  content: attr(data-text);
  position: absolute;
  bottom: -100%;
  left: 0;
  color: #000000;
}

.Page-Menu .Page-Menu-nav ul li:hover a {
  transform: translateY(-100%);
  color: #ffffff;
}

.Page-Menu-menuicon {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 60px;
  height: 60px;
  background: url(/build/img/icons/menu-bars-final.ico);
  background-size: 40px;
  cursor: pointer;
  background-repeat: no-repeat;
  background-position: 10px;
  box-shadow: 0 0 10px var(--blanco);
}

.Page-Menu-menuicon.active {
  background: url(/build/img/icons/menu_inicio.ico);
  background-size: 40px;
  background-repeat: no-repeat;
  background-position: 10px;
  box-shadow: 0 0 10px #ffffff;
}

@media (max-width: 991px) {
  .Page-Menu .Page-Menu-banner {
    display: none;
  }

  .Page-Menu .Page-Menu-nav ul li a {
    font-size: 3em;
  }
}
/*Contenido principal - main*/
.inicio {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  justify-items: center;
  width: 100vw;
  height: 100vh;
}
.inicio .main {
  width: 80vw;
  height: 80vh;
  align-items: center;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.inicio .main .info-contect {
  text-align: center;
  /*animation: mostrar 3s ease-in-out both;*/
}
.inicio .main .info-contect h1 {
  font-size: 3.3em;
  margin-bottom: 5px;
}
.inicio .main .info-contect h1 span {
  color: var(--azulfuentes);
}
.inicio .main .info-contect .description-main {
  font-family: var(--FontLora);
  font-size: 0.95em;
}
.inicio .main .img-main {
  grid-column: 2/3;
  margin: 0;
  padding: 0;
  max-width: 100%;
  max-height: 100%;
  image-rendering: pixelated;
  /*el navegador suaviza la imagen para que no se vea pixelada.*/
  /*filter: drop-shadow(0 0 5px rgb(212, 245, 255)); /*Sombrear la img*/
  /*border: 7px solid aqua;*/
}
.inicio .main .img-main:hover {
  filter: drop-shadow(0 0 0.55rem white);
  transform: perspective(500px) translate3d(10px, 0, 0px);
  background: none;
}
