  text-align: center;
}
.blog .blog-container-title p {
  margin: 0 1em;
  text-align: justify;
}
.blog .blog-container-links {
  text-align: center;
}
.blog .blog-container-btn {
  width: 70%;
  margin: 2em 0;
}
.blog .blog-container-btn a {
  text-decoration: none;
  color: #12131d;
}
@media screen and (max-width: 880px) {
  .blog {
    padding: 20px 0;
    width: 100%;
    height: 100%;
  }
  .blog .blog-container {
    grid-template-columns: repeat(1, 1fr);
    margin: 20px 0;
    max-width: 100%;
  }
  .blog .blog-card-container {
    margin: 20px 0;
  }
}

.form-contact {
  background: linear-gradient(to bottom, rgba(19, 22, 48, 0.97), #131630), url(../img/formEmail_programmer_re_owql.svg);
  background-repeat: no-repeat;
  background-position: center;
  text-align: center;
  padding: 40px 40px;
  display: flex;
  place-content: center;
  place-items: center;
}
.form-contact h2 {
  text-align: justify;
  margin-top: 1em;
}
.form-contact .form-content form {
  margin: 4em 0 0 0;
  width: 80%;
  height: 80%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0rem;
}
.form-contact .form-content label, .form-contact .form-content input, .form-contact .form-content textarea {
  font-size: 1.5em;
  margin-right: 20px;
  border-radius: 25px;
}
.form-contact .form-content .lbl-name {
  grid-column: 1/2;
}
.form-contact .form-content .name {
  grid-column: 2/3;
}
.form-contact .form-content .lbl-phone {
  grid-column: 3/4;
  grid-row: 1/2;
}
.form-contact .form-content .lbl-phone {
  grid-row: 1/2;
  grid-column: 3/4;
}
.form-contact .form-content .phone {
  grid-column: 4/5;
  grid-row: 1/2;
}
.form-contact .form-content .lbl-email {
  grid-column: 1/2;
  grid-row: 2/3;
}
.form-contact .form-content .email {
  grid-column: 2/5;
  grid-row: 2/3;
}
.form-contact .form-content .lbl-message {
  grid-column: 1/2;
  grid-row: 3/4;
}
.form-contact .form-content .message {
  grid-column: 2/5;
  grid-row: 3/4;
  width: 100%;
}
.form-contact .form-content input, .form-contact .form-content label {
  margin-bottom: 35px;
}
.form-contact .form-content input, .form-contact .form-content textarea {
  background: linear-gradient(to bottom, rgba(19, 22, 48, 0.97), #131630);
  /*filter: drop-shadow(0 0 5px rgb(212, 245, 255));
  opacity: 80%; */
  color: #fff;
  border-radius: 5px;
  padding: 0.5em 0.5em;
}
@media screen and (max-width: 890px) {
  .form-contact .form-content form {
    display: flex;
    flex-direction: column;
    padding: 3.5em 0;
    margin: 0;
  }
  .form-contact .form-content input, .form-contact .form-content textarea, .form-contact .form-content label {
    margin: 5px 0 25px 0;
    padding: auto;
    width: 100%;
    text-align: justify;
  }
  .form-contact .form-content label {
    text-align: left;
  }
  .form-contact .form-content btn-form {
    width: 100%;
    text-align: center;
  }
}
@media screen and (max-width: 540px) {
  .form-contact .form-content form {
    padding: 0;
  }
  .form-contact .form-content label, .form-contact .form-content input, .form-contact .form-content textarea {
    margin: 1px 0;
    text-align: justify;
    padding: 0.2em 0.2em;
  }
  .form-contact .form-content .btn-form {
    width: 100%;
    text-align: center;
    margin-top: 25px;
  }
}

.form-content .btn-form {
  grid-column: 2/4;
  margin-top: 30px;
}

.form-content .btn-form {
  font-family: inherit;
  font-size: 20px;
  background: royalblue;
  color: white;
  padding: 0.7em 1em;
  padding-left: 0.9em;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 16px;
  overflow: hidden;
  transition: all 0.2s;
}

.form-content .btn-form span {
  display: block;
  margin-left: 0.3em;
  transition: all 0.3s ease-in-out;
}

.form-content .btn-form svg {
  display: block;
  transform-origin: center center;
  transition: transform 0.3s ease-in-out;
}

.form-content .btn-form:hover .svg-wrapper {
  animation: fly-1 0.6s ease-in-out infinite alternate;
}

.form-content .btn-form:hover svg {
  transform: translateX(1.2em) rotate(45deg) scale(1.1);
}

.form-content .btn-form:hover span {
  transform: translateX(5em);
}

.form-content .btn-form:active {
  transform: scale(0.95);
}

@keyframes fly-1 {
  from {
    transform: translateY(0.1em);
  }
  to {
    transform: translateY(-0.1em);
  }
}
.overlay {
  background: rgba(0, 0, 0, 0.7);
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
  display: none;
}

.overlay.is-active {
  display: block;
}

input.is-active {
  position: relative;
  z-index: 3;
}

textarea.is-active {
  position: relative;
  z-index: 3;
}

/*Footer*/
.footer {
  width: 100%;
  height: 400px;
  background: linear-gradient(to bottom, #131630, #070a1f);
  text-align: center;
}

.footer-footer {
  height: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: #ffffff;
}

.footer-footer h3 {
  padding: 4em 0;
}

.footer-footer p {
  padding-top: 4em;
}

.footer-links-img {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  padding-bottom: 1.5em;
}

@media screen and (max-width: 540px) {
  .footer {
    width: 100%;
    height: 100%;
  }

  .footer-links-img {
    grid-template-columns: repeat(2, 1fr);
  }

  .footer-footer p {
    padding-top: 0em;
  }
}
.footer-links-img a {
  text-decoration: none;
  color: var(--blanco);
  padding: 8px 0;
}

.footer-links-img a img {
  width: 75px;
  height: 75px;
}